/* contactUs -------------------------------------------------- */

/*  -------------------------------------------------- */

.contactCategory {
	position: relative; //

	@include mq() {
		font-size: 1.2em;
	}

	//absolutehero
	&_wrap {
		position: absolute;
		top: -5em; //
		left: 0;
		z-index: 15; //h110
		width: 100%;
		padding-right: 10%;
		padding-left: 10%;
		font-weight: bold;
	}

	//
	&_flex {
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-end;
		height: 5em; //
	}

	//
	&_item {
		position: relative;
		display: inline-flex;
		flex: auto;
		align-items: center;
		justify-content: center;
		width: 50%;
		min-width: fit-content;
		height: 4em;
		padding-right: 1em; //
		padding-left: 1em; //
		overflow: visible;
		background-color: $g70;

		//itemtranslate
		.box {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			display: block;
			background-color: $g70;
			transition: transform 150ms; //
		}

		.text {
			z-index: 1;
			pointer-events: none;
			transition: transform 150ms; //
		}

		//
		&::after {
			position: absolute;
			bottom: rem(-16);
			left: 50%;
			display: block;
			width: rem(8);
			height: rem(24); //168
			margin-left: rem(-4); //
			content: '';
			background-color: $cl-key;
			opacity: 0;
		}

		//
		&:hover {
			.box {
				transform: translateY(-1em);
			}

			.text {
				transform: translateY(-0.5em); //
			}
		}
	}

	//
	.-active {
		background-color: $g100;

		.box {
			pointer-events: none;
			background-color: $g100;
			transform: translateY(-1em);
		}

		.text {
			transform: translateY(-0.5em); //
		}

		&::after {
			opacity: 1; //
		}
	}
}

/*  -------------------------------------------------- */

.takeout {
	&_container {
		margin-top: rem(40);

		@include mq() {
			margin-top: rem(80);
		}
	}

	//tab */
	&_tabWrap {
		position: relative;
		width: 100%;
		font-weight: bold;

		//
		&::after {
			position: absolute;
			bottom: rem(16 - 1); // - 
			left: -2%;
			// z-index: 1; //
			display: block;
			width: 104%;
			height: rem(1);
			content: '';
			background-color: $cl-black;
		}
	}

	//contents
	&_contentsWrap {
		width: 100%;
		padding-top: 0.6em;
	}

	//cart
	&_cartWrap {
		margin-top: rem(40);

		@include mq() {
			margin-top: rem(80);
		}
	}
}

/*  -------------------------------------------------- */

.takeoutMenu {
	display: flex;
	flex-flow: row wrap;
	padding-top: 2em;
	padding-bottom: 2em;
	border-bottom: rem(1) solid $cl-black;

	@include mq() {
		flex-flow: row nowrap;
		justify-content: space-between;
	}

	//mediaBox
	&_mediaBox {
		flex: none; //
		order: -1;
		width: rem(100);
		height: rem(100);
		margin-bottom: 1em; //14

		@include mq() {
			width: rem(160);
			height: rem(160);
			margin-right: 2.5em; //40
			margin-bottom: 0; //
		}
	}

	&_img {
		width: 100%;
	}

	//infoBox
	&_infoBox {
		flex: 1 1 100%;
		margin-top: calc(#{rem(-100)} + -1em); ////+

		@include mq() {
			margin-top: 0; //
		}
	}

	&_title {
		min-height: rem(100); //
		padding-left: calc(#{rem(100)} + 1em); //+
		font-size: 1.2em;
		font-weight: 700;

		@include mq() {
			min-height: auto; //
			padding-left: 0; //
		}

		a {
			&:hover {
				text-decoration: underline;
			}
		}
	}

	&_allergenBox {
		margin-top: 1em;

		span {
			display: inline-block;
			padding: 0.3em 1em;
			margin-right: 0.5em;
			font-size: 0.8em;
			background-color: $g70;
			// border: rem(1) solid $cl-black;
			border-radius: 3em;
		}
	}

	&_text {
		margin-top: 1em;
	}

	//priceBox
	&_priceBox {
		display: flex;
		flex-flow: row wrap;
		justify-content: flex-end; //
		margin-top: 1em;
	}

	&_price {
		flex: none;
		padding-right: 0.5em; //
		margin-right: auto; //
		font-size: 1.8em;
		font-weight: 700;
		color: $cl-key;

		i {
			font-size: 0.8em;
		}
	}

	//orderCountBox
	&_orderCountBox {
		display: inline-flex;
	}

	//_cartBtn
	&_cartButton {
		flex: none;
		width: 3.2em;
		height: 3.2em;
		margin-right: 0.2em; //scale
		margin-left: 1em; //
		color: $g100;
		background-color: $cl-black;
		border-radius: 50%;

		i {
			font-size: 1.5em;
		}

		&:hover {
			background-color: $cl-key;
		}

		&:active {
			transform: scale(1.1);
		}
	}
}

.takeoutForm {
	margin-top: rem(32);
}

/*  -------------------------------------------------- */

.cartItem {
	display: flex;
	flex-flow: row nowrap;
	// justify-content: space-between;
	width: 100%;
	padding-top: 1.5em;
	padding-bottom: 1.5em;
	border-bottom: rem(1) solid $cl-black;

	//mediaBox 

	&_mediaBox {
		flex: none;
		order: -1;
		width: rem(40);
		height: rem(40);
		margin-right: 1em;

		@include mq() {
			width: rem(80);
			height: rem(80);
		}
	}

	&_img {
		width: 100%;
	}

	//infoBox 

	&_infoBox {
		flex: auto;
	}

	&_title {
		font-size: 1.2em;
		font-weight: 700;
	}

	//
	&_priceBox {
		display: inline-flex;
		flex-flow: row wrap;
		align-items: center;
		justify-content: flex-end;
		// width: calc(100% + #{rem(40)} + 1em); //
		width: 100%;
		margin-top: 1em;
		margin-bottom: -1em; //
	}

	&_price {
		flex: none;
		padding-top: 1em; //
		padding-bottom: 1em; //
		margin-left: 1em; //
		font-size: 1.5em;
		font-weight: 700;
		line-height: 1;
		color: $cl-key;

		i {
			font-size: 0.8em;
		}
	}

	//deleteBox 
	&_deleteBox {
		flex: none;
		margin-left: 1.5em;
		color: $cl-key;
	}

	&_deleteButton {
		width: 2em;
		height: 100%;
		background-color: $g100;
		border: rem(2) solid currentColor;
		// border-radius: 50%;

		&:hover {
			color: $g100;
			background-color: $cl-key;
			border: rem(2) solid $cl-key;
		}

		&:active {
			transform: scale(1.1);
		}
	}
}

.cart {
	&_total {
		width: 100%;
		padding: 0.8em;
		margin-top: 3em;
		font-size: 1.2em;
		font-weight: 700;
		text-align: center;
		background-color: $g100;
		border: rem(4) solid transparentize($cl-key, 0.7);
		border-radius: 10rem;

		span {
			margin-right: 1em;
			margin-left: 1em;
			font-size: 1.5em;
			color: $cl-key;
		}
	}
}

/* other -------------------------------------------------- */

.other {
	&_wrap {
		margin-top: rem(40);

		@include mq() {
			margin-top: rem(80);
		}
	}
}
