.swiper-slide {
}

/* pagination */
.swiper-pagination {
	position: static;
	display: flex; //
	justify-content: center;
	// z-index: 10;
	// text-align: center;
	// transition: 300ms opacity;
	// transform: translate3d(0, 0, 0);
	margin-top: rem(32); // wrapper
}

/* bullets */
.swiper-pagination-bullet {
	// display: inline-block;
	width: rem(12);
	height: rem(12);
	background: $cl-key; //new
	// border-radius: 100%;
	opacity: 0.2;

	@include mq() {
		width: rem(16);
		height: rem(16);
	}

	& + & {
		margin-left: rem(20);
	}
}

//active
.swiper-pagination-bullet-active {
	background: $cl-key; //new
	opacity: 1;
}

/* progressbar */
//active
.swiper-pagination-progressbar {
	top: auto; //
	bottom: 0; // 
	height: rem(8);
	background: $cl-base-blue();
	border-radius: 10em;
}

//active
.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
	overflow: hidden;
	background: $cl-key();
	border-radius: 10em;
}

